
div.main-frame{
    max-width: 1080px;
    padding-top: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    height: 100%;
}
span.input-element{
    width: 100%;
    height:100%;
    vertical-align: text-top;
    display: inline-block;
}
span.input-element input[type="range"]{
    display: inline-block;
    width: 60%;
    line-height: 3em;
    margin-right:4%;
    vertical-align: middle;
}
span.input-element select{
    display: inline-block;
    width: 60%;
}
span.input-element input[type="text"],
span.input-element input[type="password"]{
    width: 60%;
    display: inline-block;
}
span.input-element input[type="number"]{
    display: inline-block;
    width: 24%;
    line-height: 2em;
    vertical-align: middle;
}
div.box-body div.setting-item{
    width:100%;
    min-height: 4.5em;
    padding: 1em 2em 1em 0;
    display: inline-block;
}
div.max-width-limit{
    max-width: 560px;
}
div.margin-s{
    padding-top:1em;
    padding-bottom:1em;
}
div.setting-item span.item-text{
    position:relative;
    width: 30%;
    float:left;
    height:100%;
    display: inline-block;
    text-align: center;
    line-height: 2em;
}
div.setting-item span.input-element{
    width: 60%;
    height:100%;
    vertical-align: text-top;
    display: inline-block;
}
div.setting-item span.input-element select{
    width: 80%;
    margin-bottom: 2rem;
}
span.des{
    margin-left:2em;
    color: #666666;
}
button.finish_btn{
    outline: none;
    border: none;
    width: 100%;
    background-color: #0073b7;
    color: white;
    height: 100%;
    line-height: 2.7em;
    font-size: 1.5rem;
}
span.input-element div.mask{
    position: absolute;
    width: 58%;
    height: 35px;
    background-color: rgba(255,255,255, 0.75);
    text-align: center;
}
span.input-element div.mask span.mask-text{
    font-size: 14px;
    line-height: 30px;
    display: inline-block;
    color: rgba(100,100,100,0.75);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
button.finish_btn[disabled]{
    background-color:#90a7b5;
}
a{
    cursor: pointer;
}
@media(max-width: 766px){
div.main-frame{
        padding-top: 2rem;
}
}




